@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@300&family=Poppins:wght@100;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@300&family=Poppins:wght@100;400;500&display=swap);
:root{--primary-white: #ffffff;--primary-light-grey: #f4f4f4;--primary-grey: #ccd1d9;--primary-dark-grey: #666666;--primary-orange: #f9ab00;--primary-dark: rgb(52, 58, 64);--primary-red: #ef4035;--primary-hover-red: #f33501;--primary-black: #000000;--primary-transparent-black: rgba(0, 0, 0, 0.7);--primary-green: green}body{font-family:"Barlow",sans-serif}
.logo{height:75px;width:auto}.navbar{position:fixed;top:0;width:100%;right:0;z-index:2;transition-timing-function:cubic-bezier(0, 0.97, 0, 2.15);transition:all .5s ease-in-out;-webkit-transform:translateY(-100%);transform:translateY(-100%)}.navbar.drop{-webkit-transform:translateY(0%);transform:translateY(0%)}nav i{color:var(--primary-white);font-size:1.6rem}.navbar-light .navbar-nav .nav-link{color:#fff}.navbar-light .navbar-nav .nav-link:hover{color:var(--primary-red)}.navbar-light .navbar-nav .active>.nav-link{color:var(--primary-red)}nav a.nav-link{font-size:.8rem;letter-spacing:.0375rem;cursor:pointer}nav ul li{text-transform:uppercase}.navbar-nav>li>a{margin-right:.75rem;margin-left:.75rem;color:var(--primary-white);border-top:.1875rem solid var(--primary-dark)}.navbar-brand{max-width:17.25rem;padding:0 !important;height:6rem !important}.navbar-nav>li>a.active,.navbar-nav>li>a.active:focus{border-top:.1875rem solid var(--primary-red)}.navbar-nav>li>a:hover{border-top:.1875rem solid var(--primary-red);color:var(--primary-red)}
.header-wrapper{position:relative;height:100vh;width:100%;background:url(/static/media/heroHome.1b727615.jpg) center center/cover no-repeat;overflow:hidden}.header-wrapper .overlay{position:absolute;top:0;left:0;bottom:0;right:0;z-index:0;background:linear-gradient(180deg, #000001 44%, white 100%);mix-blend-mode:multiply;opacity:.7}.header-wrapper button{margin-top:3rem}.header-wrapper .particles-canvas{position:absolute;top:0;bottom:0;left:0;right:0;z-index:1}.header-wrapper .main-info{position:absolute;top:0;bottom:0;left:0;right:0;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;z-index:1}.header-wrapper .main-info h1{font-family:"Poppins",sans-serif;color:var(--primary-red);text-transform:uppercase}@media(max-width: 768px){.header-wrapper .main-info h1{font-size:1.7rem !important;text-align:center;display:block;max-width:80%}}.header-wrapper .main-info .typed-text{font-size:1.3rem !important;color:var(--primary-white)}.header-wrapper .main-info .typed-text span{font-size:1.6rem}.header-wrapper .main-info .btn-main-offer{border:1px solid var(--primary-red);text-transform:uppercase;border-radius:0;padding:.625rem .75rem .75rem .75rem;color:var(--primary-white);margin:2rem}.header-wrapper .main-info .btn-main-offer:hover{text-decoration:none;background-color:var(--primary-hover-red);color:var(--primary-white);transition:.2s ease-in-out}
.btn{padding:8px 20px;border-radius:2px;outline:none;border:none;cursor:pointer;transition:all .3s ease-out;font-family:"Poppins",sans-serif}.btn--primary{background-color:var(--primary-red);color:var(--primary-white);border:1px solid var(--primary-red)}.btn--primary-black{background-color:var(--primary-red);color:var(--primary-black);border:1px solid var(--primary-red)}.btn--outline{background-color:transparent;color:var(--primary-white);padding:8px 20px;border:1px solid var(--primary-red);transition:all .3s ease-out}.btn--medium{font-size:20px;padding:8px 20px}.btn--large{padding:12px 26px;font-size:20px}.btn--medium:hover,.btn--large:hover{background:var(--primary-red);color:var(--primary-white);transition:all .3s ease-out}
.person{padding:5rem 0}.person .photo-wrap{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.person .profile-img{width:19rem;height:19rem;border-radius:50%;padding:.3rem;object-fit:cover;border:1px solid var(--primary-orange)}.person .about-heading{color:var(--primary-orange);text-transform:uppercase}@media(max-width: 468px){.person .profile-img{width:10rem}}
.services{text-align:center;padding:4rem 0;background:var(--primary-light-grey)}.services h1{color:var(--primary-dark);text-transform:uppercase}.services .circle{position:relative;margin:.375rem auto;background:var(--primary-orange);border-radius:50%;width:4rem;height:4rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;font-size:1.4rem;margin-bottom:2rem}.services .box{height:18rem;border-bottom:.3125rem solid var(--primary-orange);padding:.625rem;margin-bottom:1.875rem;background:var(--primary-white);transition:.3s all ease-in-out}.services .box:hover{background:var(--primary-orange);border-bottom:.3125rem solid var(--primary-dark)}.services .box:hover .icon{color:var(--primary-dark)}.services .box:hover .circle{background:var(--primary-white)}.services .box:hover p{color:var(--primary-white)}
.experience{padding:.7rem;padding-bottom:5rem}.experience-wrapper{position:relative;padding:3.125rem 0;overflow:hidden}.experience h1{text-transform:uppercase;color:var(--primary-orange)}.experience-wrapper::before{position:absolute;content:"";top:0;left:50%;margin-left:-0.0625rem;width:.125rem;height:100%;background:var(--primary-grey);z-index:1}.timeline-block{width:calc(50% + 0.5rem);display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;clear:both}.timeline-block-right{float:right}.timeline-block-left{float:left;direction:rtl}.marker{width:1rem;height:1rem;border-radius:50%;border:.3125rem solid var(--primary-dark);background:var(--primary-orange);margin-top:.625rem;z-index:3}.timeline-content{width:95%;padding:0 .9375rem;color:var(--primary-dark-grey);direction:ltr}.timeline-content h3{margin-top:.3125rem;color:var(--primary-red);font-weight:900}@media(max-width: 768px){.experience .container{padding-left:0}.experience-wrapper::before{left:.5rem;margin-bottom:1.875rem}.timeline-block-right{float:none;width:100%}.timeline-block-left{float:none;direction:ltr;width:100%;text-align:right}}
.portfolio-wrapper{background:var(--primary-light-grey);padding:3rem 0}.portfolio-wrapper h1{color:var(--primary-orange)}.portfolio-image{width:15rem;border:1px solid var(--primary-orange);padding:0 .5rem}.portfolio-image-box{position:relative;margin:1rem}.overflow{position:absolute;opacity:0;background:var(--primary-black);width:15rem;height:8rem;top:0;border:1px solid var(--primary-red);cursor:pointer}.portfolio-image-box:hover .overflow{opacity:60%;transition:.3s ease-in-out}.portfolio-icon{position:absolute;opacity:0;margin-left:-8rem;margin-top:3.6rem;color:var(--primary-orange);font-size:1.5rem}.portfolio-image-box:hover .portfolio-icon{opacity:70%;transition:.3s ease-in-out;cursor:pointer}.portfolio-image-popupbox{width:45rem;padding:0 .5rem}.hyper-link{cursor:pointer;color:var(--primary-orange)}.hyper-link:hover{color:var(--primary-red)}@media(max-width: 768px){.portfolio-image-popupbox{width:100%}}@media(max-height: 570px){.popupbox-wrapper{height:100%}.portfolio-image-popupbox{width:50%}}@media(max-height: 280px){.popupbox-wrapper{height:90%}.portfolio-image-popupbox{width:30%}}
.testimonials{background-image:url(/static/media/testimonials-bg.75612be7.jpg);background-size:cover;background-color:var(--primary-black);text-align:center;padding:5rem 0;margin-bottom:0;width:100%}.testimonials h1{color:var(--primary-orange);padding:3rem 0;text-transform:uppercase}.testimonials-content{text-align:center;max-width:53.125rem;margin:0 auto 2rem auto;background:var(--primary-transparent-black)}.myCarousel{color:var(--primary-white);background:transparent;margin-top:-6%;width:54%;margin-left:auto;margin-right:auto;padding-top:6%;padding-bottom:8%;padding-left:5%;padding-right:5%;height:17.875rem}.myCarousel h3{color:var(--primary-orange);font-weight:600;text-transform:uppercase}.myCarousel p{color:var(--primary-grey)}.carousel .slide img{width:10rem !important;border-radius:50%}.carousel .slide{background:transparent !important;height:100% !important;padding-top:2rem}.carousel-root{margin:auto !important;margin-top:3% !important}
.contacts{background:var(--primary-dark);padding:3rem 0}.contacts h1{color:var(--primary-orange);padding:1.3rem 0;text-transform:uppercase}.contacts p{color:var(--primary-light-grey);font-size:1.2rem}.contacts input,.contacts textarea{width:100%;box-sizing:border-box;background:none;resize:none;border:0;border-bottom:.125rem solid var(--primary-dark-grey);border-radius:0;color:var(--primary-orange);outline:0 !important;box-shadow:none !important;margin-bottom:1rem;padding-left:0 !important}.contacts .container{padding:4rem 0}.contacts input:focus,.contacts textarea:focus{background:transparent;color:var(--primary-orange);border-bottom:.125rem solid var(--primary-dark-grey)}.contacts textarea{height:12.2rem !important}.contact-btn{margin:1rem 0 0 0;width:100%;background:none}.line{position:relative;top:-0.8rem;width:0;height:.125rem;background:var(--primary-orange);display:inline-block}input:focus+.line,textarea:focus+.line{width:100%;transition:width .5s ease-in-out}input.form-control{margin:-0.3rem 0}textarea.form-control{margin:-0.3rem 0}.error-message{color:var(--primary-white);background:var(--primary-hover-red);text-align:left}.success-message{font-weight:900;color:var(--primary-green);text-align:center}@media(max-width: 768px){.contacts{overflow-x:hidden}.contacts .container{padding:0 1rem}.contacts p{padding:0 .4rem}}
.footer{background:var(--primary-black);color:var(--primary-dark-grey);padding:3rem}.footer a{text-decoration:none;color:var(--primary-dark-grey)}.footer a:hover{text-decoration:none;color:var(--primary-orange);cursor:pointer}.footer p{margin-bottom:0}
